@import '../../theme.scss';
#presenters {
	.presenters-grid {
		display: grid;
		grid-template-columns: 1fr 1fr;
		grid-template-rows: 1fr 1fr;
		max-width: 1100px;
		margin: 0 auto;
		padding: 30px 0;
		row-gap: 120px;
		@include mobile {
			width: 100%;
			grid-template-columns: 1fr 1fr;
			column-gap: 20px;
			row-gap: 20px;
		}
		.presenter-card {
			.presenter-img {
				width: 350px;
				height: 350px;
				background-size: cover;
				background-position: center;
				border-radius: 12px;
				position: relative;
				@include mobile {
					width: 100%;
					height: auto;
					aspect-ratio: 1;
				}

				.bubble {
					position: absolute;
					right: -50%;
					top: 50%;
					
				}
			}
			&:nth-of-type(even) {
				justify-self: end;
				.bubble {
					position: absolute;
					left: -50%;
					top: unset;
					bottom: 50%;
				}
			}
			@include mobile {
				width: 100%;
				.bubble {
					right: 0 !important;
					left: 0 !important;
					width: 100% !important;
					top: unset !important;
					bottom: 0px !important;
					border-radius: 0 0 10px 10px;
					height: 70px;
				}
			}
		}
	}
	button {
		margin: 80px auto 0 auto;
		display: block;
		@include mobile {
			margin: 40px auto 0 auto;
		}
	}
}