@import '../../theme.scss';

#schedule {
	.shcedule-item {
		display: grid;
		grid-template-columns: 70px auto;
		grid-template-rows: auto auto auto;
		column-gap: 20px;
		margin: 40px 0;
		.image {
			grid-column: 1;
			grid-row: 1 / 4;
			background-size: cover;
			background-position: center;
			border-radius: 100%;
			width: 100%;
			aspect-ratio: 1;
			align-self: center;
		}
		
		.time {
			font-size: 0.75rem;
			font-weight: 600;
			color: rgba(0, 0, 0, 0.5);
			padding-top: 5px;
			svg {
				fill: rgba(0, 0, 0, 0.5);
				height: 15px;
				transform: translateY(-2.5px);
				margin-right: 8px;
			}
		}

		.title {
			font-size: 1.1rem;
			font-weight: 700;
			padding: 0px 0 1px 0;
		}

		.presenter {
			font-size: 0.75rem;
			font-weight: 500;
			color: rgba(0, 0, 0, 0.5);
		}
	}
	.separator {
		text-align: center;
		position: relative;
		padding: 40px 0;
		.line {
			height: 2px;
			background-color: $primary;
			
		}
		.separator-text {
			color: $primary;
			border: 3px solid $primary;
			height: 45px;
			border-radius: 22.5px;
			background-color: $bg-primary;
			padding: 5px 30px;
			font-size: 0.9rem;
			line-height: 32px;
			font-weight: 600;
			display: inline-block;
			position: absolute;
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%);
			svg {
				fill: $primary;
				height: 15px;
				margin-right: 10px;
				transform: translateY(-1px);
				width: 15px;
			}
		}
	}
	.tabs-container {
		width: 95%;
		padding: 40px 10%;
		border: 5px solid $light;
		border-radius: 15px;
		margin: auto;
		background-color: rgba(255, 255, 255, 0.2);
		position: relative;
		overflow: hidden;
		margin-top: 30px;
		@include mobile {
			padding: 40px 15px 10px 15px;
		}
		h3 {
			font-size: 2rem;
			font-weight: 700;
			margin-bottom: 20px;
			text-align: center;
		}
		.tabs {
			width: 90%;
		
			margin: 40px auto 60px auto;
			display: grid;
			grid-template-columns: repeat(4, 1fr);
			border: 3px solid $primary;
			height: 55px;
			border-radius: 100px;
			position: relative;
			@include mobile {
				width: 100%;
			}
			.tab-button {
				border-radius: 100px;
				height: 50px;
				color: $grey;
				overflow: hidden;
				cursor: pointer;
				&.selected {
					color: $light;
				}
				text-align: center;
				font-size: 1rem;
				font-weight: 600;
				padding: 5px 20px;
				line-height: 45px;
				transition: all 0.1s ease-out;
				&:not(.selected):hover {
					background-color: rgba(255, 255, 255, 0.6);
					color: adjust-color($color: $primary, $lightness: -10%)
				}
			}
			.selector {
				position: absolute;
				background-color: $primary;
				top: 0;
				width: 0;
				height: 52px;
				transform: translate(-1px, -1px);
				border-radius: 100px;
				transition: all 0.2s ease-in-out;
			}
		}
		.sessions-container {
			position: relative;
			overflow: hidden;
			transition: all 0.2s ease-out;
		}
		.sessions {
			position: absolute;
			transition: all 0.3s ease-in-out;
			width: 100%;
			&.selected {
				left: 0;
			}
			&.right {
				left: 100%;
			}
			&.left {
				left: -100%;
			}
		}
	}
}